import React, { Component } from 'react';
import { Layout, Menu, Icon } from 'antd';
import { Link } from 'react-router-dom';

const { Sider } = Layout;
// const SubMenu = Menu.SubMenu;

export default class SiderCustom extends Component{
    constructor(props){
        super(props);
        const { collapsed }= props;
        this.state = {
            firstHide: true, //第一次先隐藏暴露的子菜单
            selectedKey: '', //选择的路径
            openKey: '', //打开的路径（选择的上一层）
        }
    }
    componentDidMount() {
        this.setMenuOpen(this.props);
    }
    componentWillReceiveProps(nextProps) {
        this.onCollapse(nextProps.collapsed);
        this.setMenuOpen(nextProps);
    }
    setMenuOpen = props => {
        const {path} = props;
        this.setState({
            openKey: path.substr(0, path.lastIndexOf('/')),
            selectedKey: path
        });
    };
    onCollapse = (collapsed) => {
        this.setState({
            collapsed,
            firstHide: collapsed,
        });
    };
    menuClick = e => {
        this.setState({
            selectedKey: e.key
        });
    };
    openMenu = v => {
        this.setState({
            openKey: v[v.length - 1],
            firstHide: false,
        })
    };
    render(){
        const { collapsed, firstHide, openKey, selectedKey } = this.state;
        return(
            <Sider
            width="220px"
            collapsed={collapsed}
            collapsedWidth="0"
            trigger={null}
            >   
                <div className="logoNameCont">
                    <div className="logo"/> 
                    <div className="platformName" style={collapsed?{display:'none'}:{display:'block'}}>商家管理后台</div>
                </div>
                <Menu
                    theme="dark"
                    mode="inline"
                    selectedKeys={[selectedKey]}
                    onClick={this.menuClick}
                    onOpenChange={this.openMenu}
                    openKeys={firstHide ? null : [openKey]}
                >

                    <Menu.Item key={"/app"}>
                        <Link to={"/app"}><Icon type="home" /><span>店铺管理</span></Link>
                    </Menu.Item>
                    <Menu.Item key={"/app/form"}>
                        <Link to={"/app/form"}><Icon type="form" /><span>商品管理</span></Link>
                    </Menu.Item>
                    <Menu.Item key="/app/richText">
                        <Link to={'/app/richText'}><Icon type="edit" /><span>营销管理</span></Link>
                    </Menu.Item>
                    <Menu.Item key="/app/upload">
                        <Link to={'/app/upload'}><Icon type="upload" /><span>数据统计</span></Link>
                    </Menu.Item>  
                    <Menu.Item key="/app/setting">
                        <Link to={'/app/setting'}><Icon type="edit" /><span>系统设置</span></Link>
                    </Menu.Item>
                    <Menu.Item key="/app/help">
                        <Link to={'/app/help'}><Icon type="upload" /><span>帮助中心</span></Link>
                    </Menu.Item>                                       
                    {/* <SubMenu key="/app/chart"
                    title={<span><Icon type="area-chart" /><span>图表</span></span>}
                    >
                        <Menu.Item key="/app/chart/echarts">
                            <Link to={'/app/chart/echarts'}><span>echarts</span></Link>
                        </Menu.Item>
                    </SubMenu> */}
                </Menu>
            </Sider>
        )
    }
}